import React, { useState, useEffect } from 'react';
import { gql } from 'apollo-boost';
import { Query, useQuery, useLazyQuery } from 'react-apollo';
import GeoMap from './components/GeoMap';
import {
  Container,
  Row,
  Col,
  Spinner,
  Table,
  Card,
  CardTitle,
  CardBody,
} from 'reactstrap';
import {
  FaSkullCrossbones as Skull,
  FaHandHoldingHeart as Heart,
  FaHeartbeat as Heartbeat,
} from 'react-icons/fa';
import './App.css';

const departmentsQuery = gql`
  query DepartmentsQuery {
    departments {
      department_number
      hosp
      rea
      rad
      dc
      date
    }
  }
`;

const departmentListQuery = gql`
  query DepartmentsQuery {
    date(date: "2020-06-25") {
      department_number
    }
  }
`;

const dailyReportQuery = gql`
  query DailyReportQuery($date: String!) {
    date(date: $date) {
      department_number
      hosp
      rea
      rad
      dc
      date
    }
  }
`;

const departmentQuery = gql`
  query DepartmentQuery($department_number: ID!) {
    department(department_number: $department_number) {
      date
      hosp
      rea
      rad
      dc
    }
  }
`;

function App() {
  const formatDate = (date) => {
    if (date.includes('/')) {
      const b = date.split('/');
      return new Date(`${b[2]}-${b[1]}-${b[0]}`);
    } else {
      return new Date(date);
    }
  };

  const dateToString = (date) => {
    var dd = date.getDate();

    var mm = date.getMonth() + 1;
    var yyyy = date.getFullYear();
    if (dd < 10) {
      dd = '0' + dd;
    }

    if (mm < 10) {
      mm = '0' + mm;
    }
    return yyyy + '-' + mm + '-' + dd;
  };

  const [departmentNumber, setDepartmentNumber] = useState('');
  const [property, setProperty] = useState('');
  const [date, setDate] = useState(new Date('2020-06-29'));
  const { loading, data, error } = useQuery(dailyReportQuery, {
    variables: { date: dateToString(date) },
  });

  const {
    loading: reportLoading,
    data: reportData,
    error: reportError,
  } = useQuery(departmentsQuery);

  return (
    <div className="app-container">
      <Container style={{ maxWidth: 'none' }}>
        <Row className="p-3">
          <Col>
            <h1>COVID France API</h1>
          </Col>
        </Row>
        {/* <Row className="p-3">
          <Col>
            <Card className="p-3">
              <CardTitle className="text-center">
                Nb Total Cas
                <Heartbeat />
              </CardTitle>
              <CardBody className="text-center">
                {loading ? (
                  <Spinner />
                ) : data ? (
                  data.date.reduce((total, dep) => total + dep.hosp, 0)
                ) : null}
              </CardBody>
            </Card>
          </Col>
          <Col>
            <Card className="p-3">
              <CardTitle className="text-center">
                Nb Total Décès
                <Skull />
              </CardTitle>
              <CardBody className="text-center">{12000}</CardBody>
            </Card>
          </Col>
          <Col>
            <Card className="p-3">
              <CardTitle className="text-center">
                Nb Total Rétabli
                <Heart />
              </CardTitle>
              <CardBody className="text-center">{12000}</CardBody>
            </Card>
          </Col>
        </Row> */}
        <Row className="p-3">
          <Col>
            <Card className="p-3">
              <Row>
                <Col className="p-4">
                  <Table>
                    <thead>
                      <h3>Total au {date.toDateString()}</h3>
                    </thead>
                    <tbody>
                      <tr>
                        <td>Hospitalisations</td>
                        <td>
                          {loading ? (
                            <Spinner />
                          ) : data ? (
                            data.date.reduce(
                              (total, dep) => total + dep.hosp,
                              0
                            )
                          ) : null}
                        </td>
                      </tr>
                      <tr>
                        <td>Retour à domicile (cumulés)</td>
                        <td>
                          {loading ? (
                            <Spinner />
                          ) : data ? (
                            data.date.reduce((total, dep) => total + dep.rad, 0)
                          ) : null}
                        </td>
                      </tr>
                      <tr>
                        <td>Réanimations</td>
                        <td>
                          {loading ? (
                            <Spinner />
                          ) : data ? (
                            data.date.reduce((total, dep) => total + dep.rea, 0)
                          ) : null}
                        </td>
                      </tr>
                      <tr>
                        <td>Décès (cumulés)</td>
                        <td>
                          {loading ? (
                            <Spinner />
                          ) : data ? (
                            data.date.reduce((total, dep) => total + dep.dc, 0)
                          ) : null}
                        </td>
                      </tr>
                    </tbody>
                  </Table>
                </Col>
                <Col>
                  <Container className="p-4 text-center">
                    <select
                      value={property}
                      onChange={(e) => setProperty(e.target.value)}
                      style={{
                        background: 'none',
                        border: 'solid 2px black',
                        padding: '4px 16px',
                        placeSelf: 'center center',
                      }}
                    >
                      <option value="" disabled>
                        Select a property
                      </option>
                      <option value="hosp">Hospitalisations</option>
                      <option value="rad">Retour à domicile</option>
                      <option value="rea">Réanimations</option>
                      <option value="dc">Décès</option>
                    </select>
                  </Container>
                  {reportLoading ? (
                    <Spinner />
                  ) : reportData ? (
                    <GeoMap
                      code={departmentNumber}
                      cases={reportData.departments}
                      property={property}
                      formatDate={formatDate}
                      date={date}
                      setDate={setDate}
                    />
                  ) : null}
                </Col>
              </Row>
            </Card>
          </Col>
        </Row>
        <Row className="p-3">
          <Col>
            <Card className="p-3">
              <Query query={departmentListQuery}>
                {({ loading, error, data }) => {
                  if (loading) return <Spinner />;
                  if (error) console.log(error);
                  return (
                    <select
                      style={{
                        background: 'none',
                        border: 'solid 2px black',
                        padding: '4px 16px',
                      }}
                      value={departmentNumber}
                      onChange={(e) => setDepartmentNumber(e.target.value)}
                    >
                      <option value="" disabled>
                        Choose a department
                      </option>
                      {data &&
                        data.date.map((department) => (
                          <option
                            value={department.department_number}
                            key={department.department_number}
                          >
                            {department.department_number}
                          </option>
                        ))}
                    </select>
                  );
                }}
              </Query>
              {departmentNumber ? (
                <Query
                  query={departmentQuery}
                  variables={{ department_number: departmentNumber }}
                >
                  {({ loading, error, data }) => {
                    if (loading) return <Spinner />;
                    if (error) console.log(error);
                    return (
                      <Container className="p-4">
                        <Table>
                          <thead align="center">
                            <tr>
                              <td>Date</td>
                              <td>Hospitalisations</td>
                              <td>Réanimations</td>
                              <td>Retour à domicile (cumulés)</td>
                              <td>Décès (cumulés)</td>
                            </tr>
                          </thead>
                          <tbody align="center">
                            {data &&
                              data.department
                                .sort((a, b) => a.date.localeCompare(b.date))
                                .reverse()
                                .map((entry, index) => (
                                  <tr key={index}>
                                    <td>{entry.date}</td>
                                    <td>{entry.hosp}</td>
                                    <td>{entry.rea}</td>
                                    <td>{entry.rad}</td>
                                    <td>{entry.dc}</td>
                                  </tr>
                                ))}
                          </tbody>
                        </Table>
                      </Container>
                    );
                  }}
                </Query>
              ) : null}
            </Card>
          </Col>
        </Row>
      </Container>
      <footer>
        Built with ⚡ by{' '}
        <a target="_blank" href="https://achrafash.me">
          Achraf ASH
        </a>
      </footer>
    </div>
  );
}

export default App;
