import React, { useRef, useEffect, useState } from 'react';
import { select, geoPath, geoMercator, min, max, scaleLinear, event } from 'd3';
import useResizeObserver from '../useResizeObserver';
import data from '../departments.geo.json';

const GeoMap = ({ code, cases, property, formatDate, date, setDate }) => {
  const wrapperRef = useRef();
  const svgRef = useRef();
  const dimensions = useResizeObserver(wrapperRef);
  const [selectedDepartment, setSelectedDepartment] = useState(null);
  let [firstDay, lastDay] = [
    formatDate(cases[0].date),
    formatDate(cases[cases.length - 1].date),
  ];

  lastDay = new Date('2020-06-25');

  useEffect(() => {
    const svg = select(svgRef.current);
    const wrapper = select(wrapperRef.current);
    wrapper.append('div').attr('class', 'tooltip');

    // const minProp = min(cases, (dep) => dep[property]);
    const maxProp = max(cases, (dep) => dep[property]);

    const colorScale = scaleLinear()
      .domain([0, 1, 2])
      .range(['#2ecc71', '#c23616']);

    const { width, height } =
      dimensions || wrapperRef.current.getBoundingClientRect();

    const projection = geoMercator()
      .fitSize([width, height], selectedDepartment || data)
      .precision(100);
    const pathGenerator = geoPath().projection(projection);

    // draw the map
    svg
      .selectAll('.department')
      .data(data.features)
      .join('path')
      .on('click', (feature) => {
        setSelectedDepartment(selectedDepartment === feature ? null : feature);
      })
      .on('mouseenter', function (feature) {
        wrapper
          .select('.tooltip')
          .style('opacity', '1')
          .style('top', `${event.offsetY + 30}px`)
          .style('left', `${event.offsetX + 50}px`)
          .html(() => {
            if (feature) {
              const id = feature.properties.code;
              const report = cases.filter((dep) => {
                return (
                  dep.department_number === id &&
                  formatDate(dep.date).toDateString() === date.toDateString()
                );
              })[0];
              return (
                feature &&
                '<table class="table"><thead><h6>' +
                  feature.properties.nom +
                  '</h6></thead>' +
                  '<tbody><tr><td>hosp</td><td>' +
                  report.hosp +
                  '</td><tr><td>rea</td><td>' +
                  report.rea +
                  '</td></tr><tr><td>rad</td><td>' +
                  report.rad +
                  '</td></tr><tr><td>dc</td><td>' +
                  report.dc +
                  '</td></tr></tbody></table>'
              );
            }
          });
      })
      .on('mouseleave', (feature) => {
        wrapper.select('.tooltip').style('opacity', 0);
      })
      .attr('class', 'department')
      .transition()
      .attr('fill', (feature) =>
        feature.properties.code === code ? '#4455ab' : '#d1d5ea'
      )
      .attr('stroke', 'rgb(188, 192, 211)')
      .style('opacity', 0.8)
      .attr('d', (feature) => pathGenerator(feature));

    // draw circles for each department
    svg
      .selectAll('.circle')
      .data(data.features)
      .join('circle')
      .attr('class', 'circle')
      .attr('cx', (feature) => projection(feature.properties.geopoint2d)[0])
      .attr('cy', (feature) => projection(feature.properties.geopoint2d)[1])
      .style('cursor', 'pointer')
      .on('click', (feature) => {
        setSelectedDepartment(selectedDepartment === feature ? null : feature);
      })
      .transition()
      .attr('r', (feature) => {
        if (property) {
          const id = feature.properties.code;
          const r = cases.filter((dep) => {
            return (
              dep.department_number === id &&
              formatDate(dep.date).toDateString() === date.toDateString()
            );
          })[0][property];
          return Math.sqrt((2000 * r) / maxProp);
        } else return 0;
      })
      .attr('translate', 'transform(-50%, -50%)')
      .attr('fill', (feature) => {
        if (property !== 'rad' && property !== 'dc') {
          const id = feature.properties.code;
          const r = cases.filter((dep) => {
            return (
              dep.department_number === id &&
              formatDate(dep.date).toDateString() === date.toDateString()
            );
          })[0][property];
          return colorScale(r / feature.properties.beds);
        } else {
          return property === 'dc' ? '#2980b9' : '#27ae60';
        }
      })
      .style('opacity', 0.4);
  }, [data, date, code, dimensions, selectedDepartment, property]);

  return (
    <div className="map-content">
      <div className="wrapper" ref={wrapperRef}>
        <svg ref={svgRef}></svg>
      </div>
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          padding: '16px',
        }}
      >
        <label>{date.toDateString()}</label>
        <input
          type="range"
          min="0"
          max="100"
          value={((date - firstDay) * 100) / (lastDay - firstDay)}
          onChange={(e) => {
            const days =
              ((e.target.value / 100) * (lastDay - firstDay)) / 86400000;
            let date = new Date(firstDay);
            date.setDate(date.getDate() + days);
            setDate(date);
          }}
        />
        <small>
          <i>
            {property === 'hosp'
              ? 'rouge: capacité hospitalière excédée / vert: capacité hospitalière respectée'
              : null}
          </i>
        </small>
      </div>
    </div>
  );
};

export default GeoMap;
